<template>
	<div id="contactForm">
		<ul class="list_wrap">
			<li class="list" v-for="list in myTeamData" :key="list.E_IDX">
				<router-link :to="`/User/${list.E_IDX}`">
					<div class="user_img">
						<img :src="`${baseImageUrl}${list.EMAIL}`" alt="" />
					</div>
					<div class="info_wrap">
						<div class="name">
							<p>{{ list.NAME }}</p>
						</div>
						<div class="info">
							<p>{{ list.DEPT_NAME }} / {{ list.CALL_NAME }}</p>
						</div>
					</div>
					<div class="icon_wrap">
						<font-awesome-icon icon="fa-solid fa-user" />
					</div>
				</router-link>
			</li>
		</ul>
	</div>
</template>

<script>
import { library } from "@fortawesome/fontawesome-svg-core";
import { fetchUser, instance } from "@/api/index";
import { faUser } from "@fortawesome/free-solid-svg-icons";
library.add(faUser);
export default {
	data() {
		return {
			baseImageUrl: process.env.VUE_APP_IMG_URL,
			userInfo: "",
			deptCode: "",
			deptCodeGenerate: "",
			myTeamData: {},
		};
	},
	async created() {
		try {
			const response = await fetchUser();
			const myEmail = localStorage.getItem("myEmail");
			this.$store.commit("setUserInfo", response.data);
			this.userInfo = myEmail;
			const myInfo = this.$store.state.arrUserInfo.filter(
				list => list.EMAIL === this.userInfo,
			);
			this.deptCodeGenerate = Number(myInfo.shift().DEPT_CODE);
			// console.log(this.deptCodeGenerate);
			const teamData = await instance.get("api/User", {
				params: {
					dept: this.deptCodeGenerate,
				},
			});
			this.myTeamData = teamData.data;
			console.log(this.myTeamData);
			return response;
		} catch (error) {
			console.log(error);
		}
	},
	computed: {
		myData() {
			return this.$store.state.arrUserInfo;
		},
		getData() {
			const result = this.myData.filter(
				// 새로운 배열에 담아서 객체를 반환
				list => list.EMAIL === this.userInfo,
			);
			return result;
		},
	},
};
</script>

